import slide from 'components/slide/index'
import http from 'js/http'
import submitTemp from './submit'

export default {
  name: "index",
  components: {
    slide,
    submitTemp
  },
  data() {
    return {
      loading: true,
      submitShow: false,
      addressList:[]
    }
  },
  methods:{
    getAddressList(){
      http.post('/user/address-list',{},(res)=>{
        if (res.code!==0){
          this.loading = false
          this.$vux.toast.show(res.msg)
          return
        }
        this.addressList = res.content.list
        this.loading = false
      })
    }
  },
  created(){
    this.getAddressList()
  },
  activated() {
  },
  watch: {
    '$route': function (t, f) {
      if (t.path === '/personal/address') {
        this.getAddressList()
      }
    }
  }
}



